import React, { useState } from "react";
import { useStaticQuery, graphql } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";

export default function Slideshow() {
  const [currentSlide, setSlide] = useState(0);
  function handleClickSlide(number) {
    var nextIndex = currentSlide + number;
    if (nextIndex < 0) {
      nextIndex = data.allContentfulSlideshow.nodes.length - 1;
    }
    if (nextIndex > data.allContentfulSlideshow.nodes.length - 1) {
      nextIndex = 0;
    }
    setSlide(nextIndex);
  }
  const data = useStaticQuery(graphql`
    query Slideshow {
      allContentfulSlideshow {
        nodes {
          description {
            description
          }
          image {
            gatsbyImageData
          }
          name
        }
      }
    }
  `);
  return (
    <div className="banner header-text">
      <div className="owl-banner owl-carousel">
        {data.allContentfulSlideshow.nodes.map((item, index) => {
          const image = getImage(item.image);
          const isActive = currentSlide === index ? "active" : "";
          return (
            <div
              className={`banner-item ${isActive}`}
              key={`slideshow_${index}`}
            >
              <GatsbyImage image={image} alt={item.name} />
              <div className="text-content">
                <h4>{item.name}</h4>
                <h2>{item.description.description}</h2>/
              </div>
            </div>
          );
        })}
        <button
          className="nav-button prev"
          onClick={() => handleClickSlide(-1)}
        >
          <i class="fa fa-angle-left" aria-hidden="true"></i>
        </button>
        <button className="nav-button next" onClick={() => handleClickSlide(1)}>
          <i class="fa fa-angle-right" aria-hidden="true"></i>
        </button>
      </div>
    </div>
  );
}
