import React from "react";
import Car from "./Car";
import { useStaticQuery, graphql } from "gatsby";
import { Link } from "gatsby"

export default function Offering() {
  const data = useStaticQuery(graphql`
    query LatestProducts {
      allContentfulCar(limit: 6) {
        nodes {
          brand
          fuel
          name
          offer
          price
          seats
          slug
          transmission
          year
          description {
            description
          }
          image {
            gatsbyImageData
            file {
              url
              fileName
              contentType
            }
          }
        }
      }
    }
  `);
  return (
    <div className="latest-products">
      <div className="container">
        <div className="row">
          <div className="col-md-12">
            <div className="section-heading">
              <h2>Modernist cars</h2>
              <Link to="/cars">view more <i className="fa fa-angle-right"></i></Link>
            </div>
          </div>
          {data.allContentfulCar.nodes.map((item, index) => {
            return (
              <div className="col-md-4" key={`car_${index}`}>
                <Car item={item}  />
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}
